.choose-program {
  margin: 0;
  padding: 0;
}

.choose-program__wrapper {
  background-image: url("../img/promo-background-mobil.jpg");
  background-size: 320px 302px;
  background-repeat: no-repeat;

}

.choose-program__title {
  color: #ffffff;
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;
}

.choose-program__slogan {
  color: #ffffff;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  text-transform: uppercase;
}

